<template>
<div class="role card">
	<div class="card-header">
		<span></span>
		<button class="btn btn-success btn-sm" title="" @click="add">
			<i class="iconfont icon-jiahao1"></i>
		</button>
	</div>
	<div class="card-body">
		<ul>
			<li class="header">
				<div class="name"></div>
				<div class="name"></div>
				<div class="desc"></div>
				<div class="permission"></div>
				<div class="status"></div>
				<div class="set"></div>
			</li>
			<li v-for="l in list" :key="l.id">
				<div class="name">{{l.name}}</div>
				<div class="title">{{l.title}}</div>
				<div class="desc">{{l.description}}</div>
				<div class="permission">{{renderPermission(l.permission)}}</div>
				<div class="status">
					<mt-switch v-model="l.display"></mt-switch>
				</div>
				<div class="set">
					<button type="button" class="btn btn-default" @click="edit(l.id)" title="">
						<i class="iconfont icon-pingjia"></i>
					</button>
					<button type="button" class="btn btn-default" @click="del(l)" title="">
						<i v-if="l.title!=''" class="iconfont icon-shanchu9"></i>
					</button>
				</div>
			</li>
		</ul>
	</div>
	<div class="card-footer"> {{list.length}} </div>
</div>
</template>
<script>
import { Switch, Indicator, MessageBox, Toast } from 'mint-ui';
import Vue from 'vue'
Vue.component(Switch.name, Switch);
export default {
	name: 'Admin_User_Groups',
	components: {},
	data () {
		return {
			list: []
		}
	},
	created() {},
	activated() {
		this.getRoles()
	},
	mounted() {},
	methods: {
		//
		getRoles() {
			let that = this
			this.$axios({
				method: 'GET',
				url: '/mcm/api/role?filter={"where":{}}'
			}).then(res => {
				if(res && res.statusText=="OK") {
					if(res.data && res.data.length>0) {
						that.list = res.data
					} else {
						Toast({message: '', duration: 1500});
					}
				} else {
					Toast({message: '', duration: 1500});
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '', duration: 1500});
				console.log(err)
			})
		},
		//
		add() {
			this.$router.push('/admin/user/role/0')
		},
		//
		edit(id) {
			this.$router.push('/admin/user/role/'+id)
		},
		//
		del(o) {
			let that = this
			if(o.title != '') {
				MessageBox.confirm("?").then(action => {
					if(action == "confirm") {
						that.$axios({
							method: 'DELETE',
							url: '/mcm/api/role/' + o.id
						}).then(res => {
							if(res && res.statusText == "OK") {
								Toast({message: '', iconClass: 'iconfont icon-success1'});
								setTimeout(() => {
									that.getRoles()
								}, 1500)
							} else {
								Toast({message: '', duration: 1500});
								console.log(res)
							}
						}).catch(err => {
							Toast({message: '', duration: 1500});
							console.log(err)
						})
					} else {
						console.log(action)
					}
				}).catch(action => {
					if(action == "cancel") {
						//
					} else {
						console.log(action)
					}
				})
			}
		},
		//
		renderPermission(list) {
			if(list.length>0) {
				let s = ""
				s = list.join("")
				return s
			} else {
				return ""
			}
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.role{
	.card-header{width:100%;display: flex;justify-content:space-between;}
	.card-body{width:100%;display: flex;flex-direction: column;
		li{width:100%;display: flex;font-size:0.9rem;margin-bottom: 5px;
			div{padding-left:10px;box-sizing: border-box;text-align: center;}
			.title{width:10%;}
			.name{width:10%;}
			.desc{width:30%;}
			.permission{width:30%;}
			.status{width:10%;display:flex; justify-content:center;}
			.set{width:10%;
				button{width:42px;height: 40px;}
			}
			&.header{font-weight: 700;margin-bottom: 15px;font-size:1rem;}
		}
	}
	.card-footer{text-align: right;font-size: .9rem;color: #666;}
}
</style>


