<template>
<div class="ueditor">
	<vue-ueditor-wrap 
		ref="ueditor" 
		v-model="ueditorContent" 
		:config="ueditorConfig" 
		:destroy="true" 
		:init="ueditorInit" 
		@ready="ueditorReady"
	></vue-ueditor-wrap>
</div>
</template>
<script>
import VueUeditorWrap from 'vue-ueditor-wrap'
export default {
	name: 'Admin_Article_Ueditor',
	components: { VueUeditorWrap },
	props: ['content'],
	data () {
		return {
			ueditorContent: '', //
			ueditorConfig: {
				// ,
	            // serverUrl: 'http://api.demo.com/ueditor/upload',
	            // UEditor,index.html
	            UEDITOR_HOME_URL: '/static/ueditor/',
	            // 
	            autoHeightEnabled: false,
	            // 
	            initialFrameHeight: 240,
	            // 
	            initialFrameWidth: '100%',
	            // 
	            enableAutoSave: false
			}
		}
	},
	created() {
		this.ueditorContent = this.content
	},
	mounted() {},
	methods: {
		//UEditor
		ueditorReady(editorInstance) {
			console.log(`${editorInstance.key}: `, editorInstance)
		},
		// init  window.UE init  scripts editor 
		ueditorInit() {
			//window.UE.registerUI(/* ... */)
			// init  registerButton 
			// name icon tip  hover handler  editor  name 
			/*this.$refs.ueditor.registerButton({
                name: 'test',
                icon: '/static/test-button.png',
                tip: 'this is a test tip',
                handler: (editor, name) => {
                        editor.execCommand('inserthtml', `<span>text inserted by test button</span>`)
                }
            })*/
		}
	},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.ueditor{}
</style>


