<template>
<transition
	mode="out-in"
	appear
	appear-active-class="animated fadeIn"
	enter-active-class="animated fadeIn" 
	leave-active-class="animated fadeOut"
>
<div class="adminAddCatalog" v-if="addCatalogDialog">
	<div class="panel panel-default">
		<div class="panel-heading">
			<span></span>
			<button class="btn btn-danger" @click="close">
				<i class="iconfont icon-guanbi2fill"></i>
			</button>
		</div>
		<div class="panel-body">
			<div class="item">
				<div class="title"></div>
				<div class="content">
					<input type="text" class="parentCatalog form-control" 
						placeholder="" 
						v-model="parentName" 
						@focus.stop.prevent="parentIDFocus"
						@blur.stop.prevent="parentIDBlur"
						>
					<button 
						class="btn btn-default closeChoose" 
						v-show="isCloseChoose"
						@click="doCloseChoose"
						>
						<i class="iconfont icon-quxiaodingdan"></i>
					</button>	
					<AdminCatalogList v-show="showCatalogList" :adminCatalog="catalogList"></AdminCatalogList>
				</div>
			</div>
			<div class="item">
				<div class="title"></div>
				<div class="content">
					<input type="text" v-model="catalog.name" class="form-control">
				</div>
			</div>
			<div class="item">
				<div class="title"></div>
				<div class="content">
					<input type="text" v-model="catalog.enName" class="form-control">
				</div>
			</div>
			<div class="item">
				<div class="title"></div>
				<div class="content sort">
					<mt-range 
						v-model="catalog.sort" 
						:min="1" 
						:max="10" 
						:step="1" 
						:bar-height="1"
						class="catalogSort"
						>	
					</mt-range>
					<input type="number" v-model.number="catalog.sort" @blur="sortInpBlur" @keyup="sortInpBlur" class="form-control sortInp" />
					 -> 
				</div>
			</div>
			<div class="item">
				<div class="title">/</div>
				<div class="content">
					<mt-switch v-model="catalog.display"></mt-switch>
				</div>
			</div>
		</div>
		<div class="panel-foot">
			<button class="btn btn-primary" @click="save"></button>
		</div>
	</div>
</div>
</transition>
</template>
<script>
import Vue from 'vue'
import { Switch, Range, Toast } from 'mint-ui';
Vue.component(Switch.name, Switch, Range.name, Range);

import { mapState } from 'vuex'
import AdminCatalogList from './CatalogList'
import axios from 'axios'
export default {
	name: 'AdminAddCatalog',
	components: {AdminCatalogList},
	props: ['catalogList'],
	data () {
		return {
			//
			addCatalogDialog: false,
			lachewang: {},
			//
			//catalogList: [],
			// 
			showCatalogList: false,
			// 
			isCloseChoose: false,
			//
			parentName: '',
			//
			parentLevel: 1,
			//false
			isUpdate: false,
			//
			catalog: {
				parentId: '',
				id: '',
				name: '',
				enName: '',
				sort: 1,
				display: true,
				words: 0,
				children: [],
				level:1,
				createTime: ''
			},
			//dom
			dom: null
		}
	},
	activated () {
		this.lachewang = JSON.parse(localStorage.lachewang)
		this.addCatalogDialog = this.lachewang.addCatalogDialog = false
		localStorage.lachewang = JSON.stringify(this.lachewang)
	},
	created () {},
	mounted () {
		let that = this
		//  
		this.BUS.$on('openAddCatalogDialog', t => {
			that.addCatalogDialog = t
		})
		//
		this.BUS.$on('chooseThisParent', obj => {
			//objobj
			that.parentName = obj.cl.name
			that.catalog.parentId = obj.cl.id
			that.catalog.level = obj.cl.level + 1
			that.isUpdate = obj.update
		})
		this.BUS.$on('chooseThis', obj => {
			that.dom = obj.dom
			//
			if(obj.createdAt) {
				//obj
				//
				that.isUpdate = true
				//ID
				if(obj.parentId) {
					//ID
					that.catalog.parentId = obj.parentId
					let catalogList = JSON.parse(localStorage.lachewang).catalogs
					catalogList.forEach(i => {
						if(i.id === obj.parentId) {
							//
							that.parentName = i.name
						}
					})
					//-1
					that.parentLevel = obj.level-1
				//ID	
				} else {
					//
					that.parentName = ''
					that.catalog.parentId = ''
					that.parentLevel = 0
				}
				that.catalog.id = obj.id
				that.catalog.name = obj.name
				that.catalog.enName = obj.enName
				that.catalog.sort = obj.sort
				that.catalog.display = obj.display
				that.catalog.level = obj.level
			//
			} else {
				//objIDnamelevel
				obj.update ? that.isUpdate = true : that.isUpdate = false
				//
				that.parentName = obj.name
				that.catalog.parentId = obj.id
				that.parentLevel = obj.level
				//
				that.catalog.name = ''
				that.catalog.enName = ''
				that.catalog.level = obj.level+1
				that.catalog.display = true
				//,
				let catalogList = JSON.parse(localStorage.lachewang).catalogs
				let num = 1
				catalogList.forEach(i => {
					if(i.parentId === obj.id) {
						num++
					}
				})
				that.catalog.sort = num
			}
			that.isCloseChoose = true
		})
	},
	methods: {
		///
		close () {
			//
			let lachewang = JSON.parse(localStorage.lachewang)
			lachewang.addCatalogDialog = false
			this.addCatalogDialog = false
			localStorage.lachewang = JSON.stringify(lachewang)
			//
			this.catalog = {
				parentId: '',
				id: '',
				name: '',
				enName: '',
				sort: 1,
				display: true,
				level:1,
				children: [],
				createTime: ''
			}
			this.parentName = ''
			this.isUpdate = false
			this.isCloseChoose = false
		},
		//
		save () {
			if(!this.catalog.name) {
				Toast({message: '', duration: 1500}); 
				return false;
			}
			if(!this.catalog.enName) {
				Toast({message: '', duration: 1500}); 
				return false;
			}
			//
			if(this.isUpdate) {
				this.forUpdateDoSave()
			//
			} else {
				this.forAddDoSave()
			}
		},
		//
		spread() {
			//
			let t = this.isUpdate ? 'edit' : 'add'
			//
			this.BUS.$emit('spread', {dom: this.dom, t: t})
			setTimeout(() => {
				//
				this.$store.dispatch('updateSpreadFlag', false)
			},300)
		},
		//
		forAddDoSave() {
			let that = this
			axios({
				method: 'POST',
				url: '/mcm/api/catalogs',
				data: {
					parentId: this.catalog.parentId,
					name: this.catalog.name,
					enName: this.catalog.enName,
					display: this.catalog.display,
					level: this.catalog.level,
					sort: this.catalog.sort
				}
			}).then(res => {
				if(res && res.status===200 && res.statusText=="OK") {
					//
					that.saveSuccess()
					that.BUS.$emit('updateCatalog')
					let lachewang = JSON.parse(localStorage.lachewang)
					lachewang.catalogs.push(that.catalog)
					localStorage.lachewang = JSON.stringify(lachewang)
					//
					setTimeout(() => {
						that.spread()
					},300)
				} else {
					Toast({message: '', duration: 2000});
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '', duration: 2000});
				console.log(err)
			})
		},
		// 
		forUpdateDoSave() {
			let that = this
			axios({
				method: 'PUT',
				url: '/mcm/api/catalogs/'+this.catalog.id,
				data: {
					parentId: this.catalog.parentId,
					name: this.catalog.name,
					enName: this.catalog.enName,
					display: this.catalog.display,
					level: this.catalog.level,
					sort: this.catalog.sort
				}
			}).then(res => {
				if(res && res.status===200 && res.statusText=="OK") {
					let lachewang = JSON.parse(localStorage.lachewang)
					let catalogs = lachewang.catalogs
					try {
						catalogs.forEach(item => {
							if(item.id === this.catalog.id) {
								item.parentId = this.catalog.parentId
								item.name = this.catalog.name
								item.enName = this.catalog.enName
								item.sort = this.catalog.sort
								item.level = this.catalog.level
								item.display = this.catalog.display
								throw new Info('exist')
							}
						})
					} catch (e) {
						if(e.message=='exist') throw e
					}
					localStorage.lachewang = JSON.stringify(lachewang)
					//
					that.saveSuccess()
					that.BUS.$emit('updateCatalog')
					//
					setTimeout(() => {
						that.spread()
					},300)
				} else {
					Toast({message: '', duration: 2000});
					console.log(res)
				}
			}).catch(err => {
				Toast({message: '', duration: 2000});
				console.log(err)
			})
		},
		//
		saveSuccess() {
			Toast({message: '', duration: 1200});
			setTimeout(() => {
				//2
				this.close()
			}, 1500)
		},
		//
		parentIDFocus() {
			this.showCatalogList = true
		},
		//
		parentIDBlur() {
			if(!this.parentName){
				this.catalog.parentId = ''
			}
			setTimeout(()=>{
				this.showCatalogList = false
			}, 150)
		},
		//  , 
		doCloseChoose() {
			this.parentName = ''
			this.catalog.parentId = ''
			//1
			this.catalog.level = 1
			this.isUpdate = true
			this.isCloseChoose = false
		},
		//
		sortInpBlur() {
			if(this.catalog.sort<1) {
				this.catalog.sort=1
			} else if(this.catalog.sort>10){
				this.catalog.sort=10
			}
		}
	},
	watch: {},
	computed: {
		...mapState(['server'])
	}
}
</script>

<style scoped lang="less">
.animated {
  -webkit-animation-duration: .3s;
  animation-duration: .3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.adminAddCatalog{position: fixed;left:0;top:0;z-index: 10;width: 100%;height: 100%;background-color: rgba(0,0,0,0.5);display: flex;justify-content: center;align-items: center;
	.panel{width:50vw;background-color:#fff;border-radius: .2vw;
		.panel-heading{width:100%;border-bottom: 1px solid #ddd;padding:.5vw;display: flex;justify-content: space-between;align-items: center;}
		.panel-body{padding:0 1vw;
			.item{width:100%;display: flex;align-items: center;
				.title{width:30%;display: flex;justify-content: flex-end;padding-right: 1vw;border-right: 1px solid #ddd;padding-top: 1vw;padding-bottom: 1vw;}
				.content{flex:1;padding-left: 1vw;position: relative;}
				.closeChoose{position: absolute;top:0;right: 0;z-index: 2;color:#ccc;transition: all 0.3s;}
				.icon-yuanxingxuanzhongfill,.catalogEnable.on{color:#563d7c;}
				.catalogEnable{color:#999;transition: all 0.3s;}
				.content.sort{display: flex;justify-content: space-between;align-items: center;
					.catalogSort{width:20vw;}
					.sortInp{width:60px;}
				}
			}
		}
		.panel-foot{width:100%;border-top: 1px solid #ddd;padding:.5vw;display: flex;justify-content: center;align-items: center;
			button{width:30%;}
		}
	}
}
@media screen and (max-width: 1200px) {
.adminAddCatalog{
		.panel{width:70%;border-radius: 1vw;padding:1vw;
			.panel-heading{padding-bottom: 1vw;
				span{font-size: 2vw;}
			}
			.panel-body{padding-top:0vw;
				.item{
					.title{padding-top: 2vw;padding-bottom: 2vw;padding-right: 2vw;font-size: 1.5vw;}
					.content{padding-left: 2vw;}
					.sort{font-size: 1.5vw;}
				}
			}
			.panel-foot{padding-top:2vw;}
		}
	}
}
@media screen and (max-width: 640px) {
.adminAddCatalog{
		.panel{width:90%;border-radius: 1vw;padding:2vw;
			.panel-heading{padding-bottom: 3vw;
				span{font-size: 4vw;}
			}
			.panel-body{padding-top:0vw;
				.item{
					.title{padding-top: 5vw;padding-bottom: 5vw;padding-right: 2vw;}
					.content{padding-left: 2vw;}
				}
			}
			.panel-foot{padding-top:2vw;
			}
		}
	}
}
</style>


