<template>
<div class="about card">
	<section class="card-header">{{msg}}</section>
	<article class="card-body">
		<dl>
			<dt></dt>
			<dd>
				<p>2019</p>
				<p>VUE2.Xvue-cli + vueRouter + axios + vuex + Mint UI</p>
				<p></p>
				<p></p>
			</dd>
		</dl>
		<dl>
			<dt></dt>
			<dd>
				<p> ChromeSafari  Internet Explorer  Javascript</p>
				<p></p>
				<p></p>
				<p></p>
				<p></p>
			</dd>
		</dl>
	</article>
</div>
</template>
<script>
export default {
	name: 'Admin_About',
	components: {},
	data () {
		return {
			msg: ''
		}
	},
	created (){},
	activated() {
		this.BUS.$emit('setSideActived', this.$route.name)
	},
	mounted () {},
	methods: {},
	watch: {},
	computed: {}
}
</script>
<style scoped lang="less">
.about{
	.card-body{min-height: 700px;
		dl{margin-bottom: 20px;
			dt{font-size:1.2rem;}
			dd{padding-left:20px;font-size: 1rem;}
		}
	}
}
</style>


