import { Toast } from 'mint-ui'
export default {
	showMenu: '',
	server: {
		spreadFlag: false
	},
	catalogs: [],
	homeBanner: [],
	homeCurtain: [],
	homeWorks: [],
	flink: [],
	systemConfig: {},
	//
	recombination: (list)=>{
		let arr = [];
		//
		list.forEach(a => {
			let i = a.level-1
			if(!arr[i] || arr[i].length==0) {
				arr[i] = []
			}
			let j = arr[i].length
			a.children=[]
			arr[i][j]=a
		})
		//
		if(arr[1] && arr[1].length>0) {
			//
			arr.forEach((item,index) => {
				let i = index+1
				if(arr[i] && arr[i].length>0) {
					//
					this.a.digui(item, arr[i])
				}
			})
		} 
		//arrarrarr[0]
		return arr[0]
	},
	//
	digui: (aCurrent,aNext)=>{
		aNext.forEach((an,index) => {
			//try catchthrow
			try {
				aCurrent.forEach((ac,index) => {
					//IDID
					if(an.parentId && ac.id && (ac.id === an.parentId)) {
						//children
						ac.children.push(an)
						throw new Info('exist')
					}
				})
			} catch (e) {
				if(e.message == 'exist') throw e
			}
		})
	},
	//
	getPages(count, size) {
		let pageTotal = 0
		if(count % size === 0) {
			pageTotal = parseInt(count / size)
		} else {
			pageTotal = parseInt(count / size) + 1
		}
		let pageList = []
		for( let i = 1; i <= pageTotal; i++ ) {
			pageList.push(i)
		}
		return { pageList, pageTotal }
	},
	//
	getFullTime(t) {
		let y = new Date(t).getFullYear()
		let m = new Date(t).getMonth()+1
		m = m < 10 ? ('0' + m) : m
		let d = new Date(t).getDate()
		d = d < 10 ? ('0' + d) : d
		let hh = new Date(t).getHours()
		hh = hh < 10 ? ('0' + hh) : hh
		let mm = new Date(t).getMinutes()
		mm = mm < 10 ? ('0' + mm) : mm
		let ss = new Date(t).getSeconds()
		ss = ss < 10 ? ('0' + ss) : ss
		return y+'-'+m+'-'+d+' '+hh+':'+mm+':'+ss
	},
	//, 
	uploadImg(imgData) { console.log(imgData)
		this.$axios({
			method: 'POST',
			url: '/mcm/api/file',
			async: false,
			data: imgData
		}).then(res => {
			if(res && res.data && res.data.id) {
				Toast({message: '', iconClass: 'iconfont icon-success1',duration: 1500})	
			} else {
				Toast({message: '', duration: 1500})	
				console.log(res)
			}
		}).catch(err => {
			Toast({message: '', duration: 1500})	
			console.log(err)
		})
	}
}


